import Bubble from "../../components/Bubble/Bubble"
import React from 'react'
import Calendar from '../../icons/Calendar'
import Location from '../../icons/Location'
import Button from '../../components/Button/Button'
import Section from "../../components/Section/Section"
import WelcomeImage from '../../assets/img/welcome-img.png'
import './Welcome.scss'

const Overview = () => {
	return (
		<div className="overview">
			<OverviewItem><Calendar/>2022. mrcius 19. szombat</OverviewItem>
			<OverviewItem><Location/>Budapest | Online</OverviewItem>
		</div>
	)
}


const OverviewItem = (props) => (
	<div className='overview-item'>
		{props.children}
	</div>
)

const Welcome = () => {
	return <Section container placeholder id="welcome">
		<div className='row'>
			<div className='col-md-7 col-12'>
				<div className='bubbles'>
					<Bubble title="23" subtitle="nap" corners={['bottom-left']} />
					<Bubble title="12" subtitle="ra" />
					<Bubble title="35" subtitle="perc" corners={['top-left']} />
				</div>
				<Overview />

				<h1>Informatikai Oktatsi Konferencia <span className='highlight'>2022</span></h1>
				<p>
					Lorem ipsum dolor sit amet consectetur, adipisicing elit. Optio quisquam quaerat veritatis ullam ex possimus, modi, cupiditate iste debitis sit rem minima consectetur pariatur autem dignissimos quos sed tenetur id rerum quo magni. Eos, officia libero voluptatibus voluptate facere quis esse earum reiciendis doloremque et ipsa totam voluptas repudiandae mollitia.
				</p>

				<Button glow>Tovbbi informci</Button>
			</div>
		</div>
		<img src={WelcomeImage} alt="" className='welcome-image' />
	</Section>
}

export default Welcome